<template>
  <li class="dropdown user user-menu">
    <a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown">
      <!-- hidden-xs hides the username on small devices so only the image appears. -->
      <span class="hidden-xs" v-if="auth">{{user.displayName}}</span>
      <span class="hidden-xs" v-else>Login</span>
    </a>
    <!-- Account Info and Menu -->
    <ul class="dropdown-menu"  v-if="auth">
      <li class="user-header" style="height:auto;min-height:85px;padding-bottom:15px;">
        <p>
          <span>{{user.displayName}}</span>
        </p>
      </li>
      <li class="user-footer">
        <a href="#" class="btn btn-default btn-flat btn-block" @click="logout">
          <i class="fa fa-sign-out"></i>
          <span>Logout</span>
        </a>
      </li>
    </ul>
    <ul class="dropdown-menu"  v-else>
      <li class="user-footer" style="height:auto;min-height:55px;padding-bottom:5px;">
        <a href="#" class="btn btn-default btn-flat btn-block" @click="login">
          <i class="fa fa-sign-in"></i>
          <span>Login</span>
        </a>
      </li>
    </ul>
  </li>
</template>

<script>
export default {
  name: 'UserMenu',
  props: ['user'],
  data () {
    return {
      auth: false
    }
  },
  mounted () {
    if (localStorage.getItem('user') && localStorage.getItem('user') !== 'null') {
      this.auth = true
    } else {
      this.auth = false
    }
  },
  methods: {
    logout () {
      this.$session.destroy()
      this.$store.commit('SET_USER', null)
      this.$store.commit('SET_TOKEN', null)

      if (window.localStorage) {
        window.localStorage.setItem('user', null)
        window.localStorage.setItem('token', null)
        window.localStorage.setItem('img', null)
        window.localStorage.setItem('id_satker', null)
      }

      this.$router.push('/login')
    },
    login () {
      this.$router.push('/login')
    }
  }
}
</script>



