<template>
  <div id="login">
    <img src="/static/img/Logo-Dafi-OK-horisontal-1.png" id="logo-its">
    <h1 class="text-center" style="margin-top: 5px; margin-bottom: 22px; color: white;">LOGIN</h1>

    <div class="text-center col-sm-12">
      <!-- errors -->
      <div v-if=response class="text-red"><p class="vertical-1p lead">{{response}}</p></div>

      <!-- login form -->
      <form @submit.prevent="checkCreds">
        <div class="input-group">
          <span class="input-group-addon"><i class="fa fa-envelope"></i></span>
          <input class="form-control" name="username" placeholder="Username" type="text" v-model="username">
        </div>

        <div class="input-group">
          <span class="input-group-addon"><i class="fa fa-lock"></i></span>
          <input class="form-control" name="password" placeholder="Password" type="password" v-model="password">
        </div>
        <button v-on:click.prevent="back" class="btn btn-secondary btn-lg">Back</button>
        <button type="submit" v-bind:class="'btn btn-primary btn-lg ' + loading">Login</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import faker from 'faker'

var host = 'https://data-dafi.herokuapp.com'

export default {
  name: 'Login',
  data(router) {
    return {
      section: 'Login',
      loading: '',
      username: '',
      password: '',
      response: ''
    }
  },
  methods: {
    back() {
      this.$router.push('/')
    },
    checkCreds() {
      // console.log('username: ' + this.username + ' Password: ' + this.password)

      this.toggleLoading()
      this.resetResponse()
      this.$store.commit('TOGGLE_LOADING')

      /* Making API call to authenticate a user */
      axios({
        method: 'post',
        url: host + '/api/login/',
        data: {
          username: this.username,
          password: this.password
        }
      }).then(response => {
        this.toggleLoading()

        var data = response.data
        /* Checking if error object was returned from the server */
        if (data.status !== 200) {
          this.response = 'Invalid Username/Password combination! Please try again'
          return
        }

        /* Setting user in the state and caching record to the localStorage */
        if (data.user) {
          var token = 'Bearer ' + data.token

          console.log(data.user)

          this.$store.commit('SET_USER', data.user)
          this.$store.commit('SET_TOKEN', token)
          this.$session.start()
          this.$session.set('jwt', data.token)

          if (window.localStorage) {
            window.localStorage.setItem('user', JSON.stringify(data.user))
            window.localStorage.setItem('token', token)
            window.localStorage.setItem('img', faker.image.avatar())
          }

          this.$router.push(data.redirect ? data.redirect : '/')
        }
      })
      .catch(error => {
        this.$store.commit('TOGGLE_LOADING')
        console.log(error)

        this.response = 'Server appears to be offline'
        this.toggleLoading()
      })
    },
    toggleLoading() {
      this.loading = this.loading === '' ? 'loading' : ''
    },
    resetResponse() {
      this.response = ''
    }
  }
}
</script>

<style>
#login {
  padding: 10em;
  position: relative;
  top: -100px;
}

html,
body,
.container-table {
  height: 100%;
  background-color: #282b30 !important;
}
.container-table {
  display: table;
  color: white;
}
.vertical-center-row {
  display: table-cell;
  vertical-align: middle;
}
.vertical-20p {
  padding-top: 20%;
}
.vertical-10p {
  padding-top: 10%;
}
.vertical-5p {
  padding-top: 5%;
}
#logo-its {
  position: relative;
  display: block; 
  width: 20em; 
  padding: 2em; 
  margin-left: auto; 
  margin-right: auto;
}

.input-group {
  padding-bottom: 2em;
  height: 4em;
  width: 100%;
}

.input-group span.input-group-addon {
  width: 2em;
  height: 4em;
}

@media (max-width: 1241px) {
  .input-group input {
    height: 4em;
  }
}
@media (min-width: 1242px) {
  form {
    padding-left: 20em;
    padding-right: 20em;
  }

  .input-group input {
    height: 6em;
  }
}

.input-group-addon i {
  height: 15px;
  width: 15px;
}
</style>



