<template>
  <!-- Main content -->
  <section class="content">
    <img src="/static/img/Logo-Dafi-OK-horisontal-1.png" id="logo-its">
    <h1 class="text-center" style="margin-top: 5px; margin-bottom: 22px; color: black;">HALAMAN DASHBOARD DATA PEGAWAI DAFI</h1>
    <!-- GitHub hint -->
    <div class="row">
      <div class="col-xs-12" v-if="auth">
        <alert :dismissible="false"
               type="success"
               :iconClasses="['fa', 'fa-check']"
               :title='message'>
        </alert>
      </div>
      <div class="col-xs-12" v-else>
        <alert :dismissible="false"
               type="danger"
               :iconClasses="['fa', 'fa-times-circle-o']"
               title='Anda belum login!'>
          <hr>
          <p>
           Silahkan melakukan login terlebih dahulu.
          </p>
        </alert>
      </div>
      <!-- /.col -->
    </div>
    <!-- /.row -->
  </section>
  <!-- /.content -->
</template>

<script>
import Alert from '../widgets/Alert'
import InfoBox from '../widgets/InfoBox'
import ProcessInfoBox from '../widgets/ProcessInfoBox'

export default {
  name: 'Dashboard',
  components: {
    Alert,
    InfoBox,
    ProcessInfoBox
  },
  data () {
    return {
      message: 'Selamat Datang, ',
      auth: false,
      generateRandomNumbers (numbers, max, min) {
        var a = []
        for (var i = 0; i < numbers; i++) {
          a.push(Math.floor(Math.random() * (max - min + 1)) + max)
        }
        return a
      }
    }
  },
  mounted () {
    if (localStorage.getItem('user') && localStorage.getItem('user') !== 'null') {
      this.auth = true
      this.message = this.message + JSON.parse(localStorage.getItem('user')) + '!'
    } else {
      this.auth = false
    }
  }
}
</script>
<style>
.info-box {
  cursor: pointer;
}
.info-box-content {
  text-align: center;
  vertical-align: middle;
  display: inherit;
}
.fullCanvas {
  width: 100%;
}
</style>



