<template lang='html'>
  <div>
    <transition name="snackbar">
      <div v-if="snackActive" class="snackbar snackbar--top-center">
        <div class="snackbar__wrap" v-bind:style="{ backgroundColor: snackBg, color: snackColor }">
          <div class="snackbar__body">
            {{ snackMessage }}
            <button class="close-btn" @click="snackActive = false">X</button>
          </div>
        </div>
      </div>
    </transition>
    <div class='wrapper-jexcel'>
      <input
        class='btn-primary'
        type='button'
        value='Tambah baris baru'
        @click='jExcelObj.insertRow()'
      />
      <input
        class='btn-primary'
        type='button'
        value='Hapus baris'
        @click='jExcelObj.deleteRow()'
      />
      <br />
      <br />
      <div id='spreadsheet' ref='spreadsheet'></div>
    </div>
  </div>
</template>

<script>
import 'jexcel/dist/jexcel.css' // eslint-disable-line no-unused-vars
import jexcel from 'jexcel' // eslint-disable-line no-unused-vars
import axios from 'axios'

var host = 'https://data-dafi.herokuapp.com'

export default {
  name: 'DataPegawai',
  data() {
    return {
      template: this.$route.meta.tableConfig,
      data: [],
      snackActive: false,
      snackTimer: null,
      snackMessage: 'Hey Hey People',
      snackBg: '#38a5e4',
      snackColor: 'white'
    }
  },
  computed: {
    jExcelOptions() {
      return {
        data: this.data,
        columns: this.template,
        onchange: this.changed,
        oninsertrow: this.insertedRow,
        ondeleterow: this.deleteRow,
        responsive: true,
        tableoverflow: true,
        wordWrap: true,
        search: true,
        pagination: 25
      }
    },
    // return column with `date` type
    dateCol: function() {
      let temp = this.template.filter(function (template) {
        return (template.type === 'calendar')
      })
      return temp.map(col => (col.name))
    }
  },
  methods: {
    load() {
      axios.get(host + '/api/pegawai').then(response => {
        var dateCols = this.dateCol

        function convertDate(str) {
          let dateEl = (str.split('T'))[0]
          return dateEl
        }

        this.data = response.data.map((obj) => {
          for (var col in dateCols) {
            if (obj[dateCols[col]] !== null && obj[dateCols[col]] !== '') obj[dateCols[col]] = convertDate(obj[dateCols[col]])
          }
          return obj
        })
        console.log(this.data)
      }).catch((err) => {
        this.displayNotif('Load error', 'err')
        console.log(err)
      })
    },
    changed(instance, cell, x, y, value) {
      x = parseInt(x)
      y = parseInt(y)

      console.log('x: ' + x + '\ny: ' + y)

      if (this.template[x].type === 'calendar') {
        let dateTemp = value.split('/').reverse().join('-')
        value = dateTemp
      }

      // get one row from table
      var id = y
      var temp = {}
      var table = document.getElementById('spreadsheet').jexcel
      // console.log(table)
      temp = table.getRowData(id)
      temp[x] = value || null

      // Convert array[] into JSON obj
      var obj = {}
      var i = 0
      Object.entries(this.data[0]).forEach(([key, value]) => {
        obj[key] = temp[i] || null
        i += 1
      })

      axios({
        method: 'put',
        url: host + '/api/pegawai/' + obj['id'],
        data: obj
      })
      .then((response) => {
        this.displayNotif('Berhasil update!', 'ok')
        console.log(response.data)
      })
      .catch(err => {
        this.displayNotif('Gagal melakukan update', 'err')
        console.log(err)
      })
    },
    insertedRow(instance) {
      axios({
        method: 'post',
        url: host + '/api/pegawai/',
        data: {
          'nama': null
        }
      }).then((response) => {
        this.displayNotif('Berhasil update!', 'ok')
        console.log(response.data)
      }).catch(err => {
        this.displayNotif('Gagal menambah baris', 'err')
        console.log(err)
      })
    },
    deleteRow(instance, row) {
      console.log(row)
      axios.get(host + '/api/pegawai/').then(res => {
        console.log(res.data[row])
        var index = Object.values(res.data[row])
        axios.delete(host + '/api/pegawai/' + index[0])
      }).then((response) => {
        this.displayNotif('Berhasil menghapus baris!', 'ok')
      }).catch(err => {
        this.displayNotif('Gagal menghapus baris', 'err')
        console.log(err)
      })
    },
    setTimer() {
      var self = this

      if (this.snackTimer) clearTimeout(this.snackTimer)

      this.snackTimer = setTimeout(function () {
        self.snackActive = false
      }, 1900)
    },
    displayNotif(message, type) {
      if (type === 'err') {
        this.snackColor = 'white'
        this.snackBg = '#cc3300'
      } else if (type === 'warn') {
        this.snackColor = 'black'
        this.snackBg = '#EED202'
      } else {
        this.snackColor = 'white'
        this.snackBg = '#339900'
      }
      this.snackMessage = message
      this.snackActive = true
    }
  },
  created() {
    this.load()
  },
  watch: {
    data () {
      const jExcelObj = jexcel(this.$refs['spreadsheet'], this.jExcelOptions)
      Object.assign(this, { jExcelObj })
    },
    snackActive() {
      this.setTimer()
    }
  }
}
</script>

<style lang='css' scoped>
.introduction {
  font-size: 14px;
  padding: 0.5em;
  margin-bottom: 0.3em;
  color: gold;
  background: #111;
}

.wrapper-jexcel {
  overflow-x: auto;
  margin-left: 0.5em;
}

h3,
h1 {
  margin-top: 5px;
  margin-bottom: 2px;
}

.close-btn {
  padding: 8px 12px;
  margin: -8px -6px -8px 20px;
  color: #000000;
  font-family: inherit;
  font-size: inherit;
  text-transform: uppercase;
  border: 0;
  border-left: 0.5px solid rgba(255, 255, 255, 0.808);
  background-color: transparent;
  cursor: pointer;
  outline: 0;
  transition: background-color .25s ease;
}

.snackbar {
  position: fixed;
  display: flex;
  will-change: transform, opacity;
  z-index: 9999;
  max-height: 60px;
}
.snackbar--top-center {
  left: 0;
  right: 0;
  top: 0;
}
.snackbar__wrap {
  width: auto;
  min-width: 300px;
  max-width: 550px;
  margin: 12px auto;
  padding: 8px;
  border-radius: 3px;
  box-sizing: border-box;
  border-radius: 5px;
}
.snackbar__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  line-height: 1.5;
  margin-left: 8px;
  margin-top: 0;
}

/* Animation */
.snackbar-enter-active, .snackbar-leave-active {
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.snackbar--top-center.snackbar-enter,
.snackbar--top-center.snackbar-leave-to {
  opacity: .5;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
</style>



