import DashView from './components/Dash.vue'
import LoginView from './components/Login.vue'
import NotFoundView from './components/404.vue'

// Import Views - Dash
import DashboardView from './components/views/Dashboard.vue'
import DataPegawaiView from './components/views/DataPegawai.vue'

// Routes
const routes = [
  {
    path: '/login',
    component: LoginView,
    meta: {onlyNonAuth: true}
  },
  {
    path: '/',
    component: DashView,
    children: [
      {
        path: 'dashboard',
        alias: '',
        component: DashboardView,
        name: 'Dashboard',
        meta: {description: 'Halaman Dashboard', requiresAuth: true}
      }, {
        path: 'pegawai',
        component: DataPegawaiView,
        name: 'DataPegawai',
        meta: {
          description: 'Tabel Data Pegawai',
          tableConfig: [
            { type: 'hidden', title: 'ID', width: '120px', name: 'id' },
            { type: 'text', title: 'Nama', width: '300px', name: 'nama' },
            { type: 'text', title: 'Alamat', width: '300px', name: 'alamat' },
            { type: 'text', title: 'Tempat Lahir', width: '110px', name: 'tempat_lahir' },
            { type: 'calendar', title: 'Tgl Lahir', width: '110px', name: 'tgl_lahir' },
            { type: 'text', title: 'Status', width: '110px', name: 'status' },
            { type: 'text', title: 'Pendidikan Terakhir', width: '80px', name: 'pendidikan' },
            { type: 'text', title: 'Nama Pasangan', width: '300px', name: 'nama_pasangan' },
            { type: 'text', title: 'Pendidikan Pasangan', width: '80px', name: 'pendidikan_pasangan' },
            { type: 'text', title: 'Pekerjaan Pasangan', width: '80px', name: 'pekerjaan_pasangan' },
            { type: 'numeric', title: 'Jumlah Anak', width: '80px', name: 'jml_anak' },
            { type: 'text', title: 'Nama Anak 1', width: '300px', name: 'nama_anak1' },
            { type: 'text', title: 'Pendidikan Anak 1', width: '80px', name: 'pendidikan_anak1' },
            { type: 'text', title: 'Nama Anak 2', width: '300px', name: 'nama_anak2' },
            { type: 'text', title: 'Pendidikan Anak 2', width: '80px', name: 'pendidikan_anak2' },
            { type: 'text', title: 'Nama Anak 3', width: '300px', name: 'nama_anak3' },
            { type: 'text', title: 'Pendidikan Anak 3', width: '80px', name: 'pendidikan_anak3' },
            { type: 'text', title: 'Nama Anak 4', width: '300px', name: 'nama_anak4' },
            { type: 'text', title: 'Pendidikan Anak 4', width: '80px', name: 'pendidikan_anak4' },
            { type: 'text', title: 'Nama Anak 5', width: '300px', name: 'nama_anak5' },
            { type: 'text', title: 'Pendidikan Anak 5', width: '80px', name: 'pendidikan_anak5' }
          ],
          requiresAuth: true
        }
      }
    ]
  }, {
    // not found handler
    path: '*',
    component: NotFoundView
  }
]

export default routes



